* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #2c3e50;
  background-color: #f9fbfc;
  font-family: Segoe UI, Roboto, sans-serif;
  transition: all .3s;
}

.app-header {
  text-align: center;
  background: #fefefe linear-gradient(#fff, #f9fbfc);
  border-bottom: 2px solid #e0e0e0;
  border-radius: 0 0 12px 12px;
  padding: 24px 32px;
  transition: all .3s;
  position: relative;
  box-shadow: 0 2px 6px #0000000f;
}

.heading {
  justify-content: center;
  align-items: center;
  gap: 16px;
  display: inline-flex;
}

.app-header h1 {
  color: #007bff;
  margin-bottom: 6px;
  font-size: 28px;
  font-weight: 600;
}

.logo i {
  color: #f39c12;
  font-size: 30px;
}

.app-header p {
  color: #555;
  opacity: .9;
  font-size: 14px;
}

.toggle-btn {
  cursor: pointer;
  background-color: #0000;
  border: 2px solid #007bff;
  border-radius: 50px;
  justify-content: center;
  align-items: center;
  width: 26px;
  height: 26px;
  padding: 0;
  transition: all .3s;
  display: flex;
  position: absolute;
  top: 24px;
  right: 32px;
}

.toggle-btn:hover {
  background-color: #e6f0ff;
  box-shadow: 0 0 6px #007bff4d;
}

.toggle-btn i {
  color: #007bff;
  font-size: 12px;
  transition: color .3s;
}

.toggle-btn i:hover {
  color: #0056b3;
}

.app-container {
  text-align: center;
  background-color: #f5f7f9;
  border-radius: 12px;
  max-width: 900px;
  margin: 40px auto;
  padding: 32px;
  box-shadow: 0 4px 12px #0000000d;
}

.btn, .map-btn {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 8px;
  margin: 20px auto;
  padding: 12px 24px;
  font-size: 15px;
  font-weight: 600;
  transition: background-color .3s;
  display: inline-block;
}

.btn:hover {
  background-color: red;
}

.map-btn:hover {
  background-color: green;
}

.location-info {
  text-align: center;
  text-align: left;
  background-color: #f1f3f5;
  border-left: 4px solid #007bff;
  border-radius: 8px;
  margin: 20px 0;
  padding: 16px 20px;
  font-size: 15px;
}

.map-container {
  border-radius: 12px;
  margin-top: 20px;
  overflow: hidden;
  box-shadow: 0 2px 6px #0000001a;
}

.map-container iframe {
  border: none;
  width: 100%;
  height: 400px;
}

body.dark-mode {
  color: #f0f0f0;
  background-color: #0d0d0d;
}

body.dark-mode .app-header {
  background: #000;
  border-bottom: 2px solid #222;
  box-shadow: 0 2px 4px #ffffff0d;
}

body.dark-mode .heading h1 {
  color: orange;
}

body.dark-mode .app-header i {
  color: #007bff;
}

body.dark-mode .app-header p {
  color: #fff;
}

body.dark-mode .app-container {
  background-color: #1a1a1a;
  box-shadow: 0 4px 12px #ffffff0d;
}

body.dark-mode .btn, body.dark-mode .map-btn {
  color: #000;
  background-color: orange;
  border-radius: 8px;
}

body.dark-mode .btn:hover {
  background-color: tomato;
}

body.dark-mode .map-btn:hover {
  background-color: #32cd32;
}

body.dark-mode .location-info {
  color: #fff;
  background-color: #2c2c2c;
  border-left: 4px solid orange;
}

body.dark-mode .map-container {
  box-shadow: 0 2px 6px #ffffff1a;
}

body.dark-mode .toggle-btn {
  background-color: #0000;
  border: 2px solid orange;
  border-radius: 50px;
}

body.dark-mode .toggle-btn:hover {
  background-color: #333;
  box-shadow: 0 0 6px #ffa5004d;
}

body.dark-mode .toggle-btn i {
  color: orange;
}

@media (width <= 600px) {
  .app-header {
    padding: 16px 20px;
  }

  .app-header h1 {
    font-size: 22px;
  }

  .app-header p {
    font-size: 12px;
  }

  .app-container {
    margin: 20px 10px;
    padding: 20px;
  }

  .btn, .map-btn {
    border-radius: 8px;
    padding: 10px 16px;
    font-size: 14px;
  }

  .location-info {
    padding: 12px 16px;
    font-size: 14px;
  }

  .map-container iframe {
    height: 300px;
  }
}
